@font-face {
  font-family: SegoeUI_bold;
  src: url("/static/media/Segoe UI Bold.b00928e6.ttf") format("truetype");
}
@font-face {
  font-family: SegoeUI_medium;
  src: url("/static/media/Segoe UI.45472284.ttf") format("truetype");
}
